.match{
  /* margin:1em; */
  display: -ms-flexbox;
  display: flex;
  /* flex-wrap: wrap; */
  -ms-flex-pack: center;
      justify-content: center;
  margin-bottom: 1em;
  -webkit-box-shadow: 7px 7px 20px  black;
          box-shadow: 7px 7px 20px  black;
  background-color: #34495e;
  /* border: 1px solid #b38600; */
  /* background: linear-gradient(to bottom right, #34495e , #0f0f0f); /* Standard syntax (must be last) */

}

.myButton{
  background-color: #16a085;
  border: none;
  color: white;
  padding: 1em;
  text-align: center;
  font-size: 16px;
  margin: 4px 2px;
  cursor: pointer;
  border: 1px solid white;
}

.myButton:hover{
  border: 1px solid black;
}

.matchInfo{
  /* position: relative; */
  /* bottom: 5em; */
  /* order: 1; */
  border-width: 0 0 0 0;

  padding: 1em;

  /* width: 9em; */
  /* padding-bottom: 0; */
  /* background-color: #2c3e50; */
  /* display: inline-block; */
}

.blueGame{
  /* background-color: #3498db; */
  background: -webkit-gradient(linear, left top, right bottom, from(#34495e), to(#3498db));
  background: -webkit-linear-gradient(top left, #34495e, #3498db);
  background: -o-linear-gradient(top left, #34495e, #3498db);
  background: linear-gradient(to bottom right, #34495e, #3498db);
  /* background: linear-gradient(to bottom right, #34495e 50%, #3498db) */
}
.redGame{
  background: -webkit-gradient(linear, left top, right bottom, from(#34495e) , to(#d35400) );
  background: -webkit-linear-gradient(top left, #34495e , #d35400 );
  background: -o-linear-gradient(top left, #34495e , #d35400 );
  background: linear-gradient(to bottom right, #34495e , #d35400 )
  /* background-color: #d35400; */
}

@media only screen and (max-width: 570px) {
    .matchInfo {
        -ms-flex-order: 1;
            order: 1;
        width: 100%;
    }
    .match{
      -ms-flex-wrap: wrap;
          flex-wrap: wrap;
    }
}



