.SumInfo{
  /* background-color: #34495e; */
  /* background-color: black; */
  background-color: #34495e;
  background: -webkit-gradient(linear, left top, right bottom, from(#34495e) , to(#0f0f0f));
  background: -webkit-linear-gradient(top left, #34495e , #0f0f0f);
  background: -o-linear-gradient(top left, #34495e , #0f0f0f);
  background: linear-gradient(to bottom right, #34495e , #0f0f0f);
  color: #b38600;
  font-weight: bolder;
  font-size: 1.1em;
  -webkit-box-shadow: 5px 5px 20px  black;
          box-shadow: 5px 5px 20px  black;
  border: 1px solid #b38600;
}

.SumInfo img{
  border: 1px solid #b38600;
  max-width: 5em;
  -webkit-box-shadow: 5px 5px 20px  black;
          box-shadow: 5px 5px 20px  black;
  margin: 10px 0;
}



